<template>
  <div class="common-admin">
    <div class="head">当前剩余车位:{{ stallNum }}</div>
    <div class="content">
      <div class="video">
        <img
          class="video-img"
          src="http://dragoncam.local:81/stream"
          alt="视频加载中"
        />
      </div>
      <div class="show">
        <div class="state">闸机状态：{{ gateState }}</div>
        <div>车牌号为:{{ carNumber }}</div>
        <div class="manual-input">
          手动输入<input v-model="carNumber" type="text" />
        </div>
        <div class="btn">
          <button @click="subNumber(1)">车辆进入</button>
          <button @click="subNumber(0)">车辆外出</button>
          <div class="checkbox-wrap">
            常开<input
              type="checkbox"
              ref="openRef"
              @click="openCheck($event)"
            />
          </div>
          <div class="checkbox-wrap">
            常闭<input
              type="checkbox"
              ref="closeRef"
              @click="closeCheck($event)"
            />
          </div>
          <button @click="gateSetting('close')">手动关闭</button>
        </div>
      </div>
    </div>
    <div class="foot">
      <div class="common-wrap">
        <div class="user-type">用户类型:{{ userType }}</div>
        <div class="count-time">剩余时间:{{ remainTime }}</div>
        <div class="income">
          应收费用:{{ legalFee }}
          <!-- <button @click="foo">确认</button> -->
        </div>
      </div>
      <div class="super-wrap" v-if="this.commonType == 1">
        超级管理员模块
        <div class="set-income">
          <input
            type="text"
            placeholder="请输入临时用户费用"
            v-model="tempFee"
          />
          <button @click="setFee">确认</button>
        </div>
        <div class="enter-month-user">
          注册长租用户:
          <hr />
          <input type="text" placeholder="请输入长租车牌号" v-model="vipCar" />
          <input type="text" placeholder="租用时间" v-model="vipTime" />
          <select v-model="selectId" @change="changeSelect($event)">
            <option value="Day">日</option>
            <option value="Month">月</option>
          </select>
          <button @click="setVip">确认</button>
        </div>
        <div class="blackList">
          添加黑名单:
          <hr />
          <input
            type="text"
            placeholder="输入黑名单车牌号"
            v-model="blackNumber"
          />
          <input type="text" placeholder="输入拉黑原因" v-model="blackCause" />
          <button @click="addBlack">确认</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    let IP = this.$route.query.serverIP;
    return {
      gateState: "",
      socket: "",
      path: `http://${IP}:8080/login`,
      // 剩余车位
      stallNum: 0,
      // 车牌号
      carNumber: "",
      commonType: this.$route.query.userType,
      // 修改费用
      tempFee: "",
      // 注册长租用户
      vipCar: "",
      vipTime: "",
      selectId: "Day",
      // 出库显示
      userType: 0,
      remainTime: 0,
      legalFee: 0,
      // 黑名单
      blackNumber: "",
      blackCause: "",
    };
  },
  async mounted() {
    await this.getSurplus();
    this.init();
    this.gateSetting("getstate");
    setInterval(() => {
      this.getSurplus();
      this.init();
      this.gateSetting("getstate");
    }, 10000);
  },
  destroyed() {
    this.socket.onclose = this.close;
  },
  methods: {
    foo() {
      axios({
        url: `${this.path}/car`,
        params: {
          type: "0",
          carid: this.carNumber,
        },
      }).then((res) => {
        console.log('11' + res.data);
        // this.userType = ;
        // this.remainTime = ;
        // this.legalFee = ;
      });
    },
    subNumber(type) {
      this.gateSetting("open");
      this.getSurplus()
      axios({
        url: `${this.path}/car`,
        params: {
          type: type,
          carid: this.carNumber,
        },
      })
    },
    // 添加黑名单
    addBlack() {
      axios({
        url: `${this.path}/black`,
        params: {
          carid: this.blackNumber,
          reason: this.blackCause,
        },
      })
    },
    // 检查车牌号
    checkCarNumber(number) {
      const reg =
        /^[京津晋冀蒙辽吉黑沪苏浙皖闽赣鲁豫鄂湘粤桂琼渝川贵云藏陕甘青宁新][ABCDEFGHJKLMNPQRSTUVWXY][\dABCDEFGHJKLNMxPQRSTUVWXYZ]{5}$/;
      let test = reg.test(number);
      console.log(test);
    },
    // 常开开闭
    openCheck(event) {
      if (event.target.checked == true) {
        this.gateSetting("constantopen", "true");
      } else {
        this.gateSetting("constantopen", "false");
      }
    },
    closeCheck(event) {
      if (event.target.checked == true) {
        this.gateSetting("constantclose", "true");
      } else {
        this.gateSetting("constantclose", "false");
      }
    },
    // 获取剩余车位
    getSurplus() {
      // console.log("send");
      axios({
        url: `${this.path}/remain`,
        method: "GET",
      }).then((res) => {
        // console.log(res);
        this.stallNum = res.data;
      });
    },
    // 建立websocket连接
    init() {
      this.socket = new WebSocket(`ws://localhost:8181`);
      this.socket.onopen = this.open;
      this.socket.onerror = this.error;
      this.socket.onmessage = this.getMessage;
    },
    open() {
      console.log("socket连接成功");
    },
    error() {
      console.log("socket错误");
    },
    getMessage(msg) {
      console.log('ws' + msg.data);
      let data = JSON.parse(msg.data);
      if (data.carid) {
        this.carNumber = data.carid;
      }
      if (data.type == 0) {
        this.userType = "临时用户";
        this.remainTime = 0;
        this.legalFee = data.remain;
      } else if (data.type == 1) {
        this.userType = "长租用户";
        this.remainTime = data.remain;
        this.legalFee = 0;
      }
    },
    close() {
      console.log("关闭连接");
    },
    // 开关闸机
    gateSetting(command, on = "null") {
      let url = "http://dragonnode.local/gate";
      if (on == "null") {
        axios({
          url: url,
          params: {
            command: command,
          },
        }).then((res) => {
          console.log(res.data);
          if (command == "getstate") {
            this.gateState = res.data;
          }
          if (res.data == "CONSTANT_OPEN") {
            this.$refs.openRef.checked = true;
          } else if (res.data == "CONSTANT_CLOSE") {
            this.$refs.closeRef.checked = true;
          }
        });
      } else {
        axios({
          url: url,
          params: {
            command: command,
            on: on,
          },
        }).then((res) => {
          console.log(res);
        });
      }
    },
    // 修改临时车费用
    setFee() {
      axios({
        url: `${this.path}/standard`,
        method: "GET",
        params: {
          fee: this.tempFee,
        },
      }).then((res) => {
        console.log(res);
      });
    },

    changeSelect(event) {
      // console.log(event.target.value)
      this.selectId = event.target.value;
    },
    // 注册长租用户
    setVip() {
      axios({
        url: `${this.path}/vip`,
        method: "GET",
        params: {
          carid: this.vipCar,
          num: this.vipTime,
          type: this.selectId,
        },
      }).then((res) => {
        console.log(res);
      });
    },
  },
};
</script>

